@charset "UTF-8";
.guide{
	width:980px;
	margin: 15px auto;
	
	.guide_h1{
		font-size:2em;
	}
	.guide_h2{
		font-size:1.6em;
		margin:40px 0 5px;
	}
	.guide_h3{
		// line-height:1.4;
		margin:10px 0 5px;	
	}
	.guideSummary{
