.thumbList{
	a,
	a:hover,
	a:active{
		color:#666;
	}
	line-height:1.4;
	h3{
		font-weight:bold;
		font-size:16px;
		margin-bottom:1em;
	}
	div{
		font-size:14px;
		margin-bottom:0.3em;
		&.listData{
			font-size:13px;
		}
	}
}
.thumbList{
	&.type01{
		figure{
			@extend .clearfix;
			.imgBox{
				width:20%;
				float:left;
				img{
					max-width:100%;
				}
			}
			figcaption{
				width:80%;
				float:left;
				padding-left:1%;
			}		
		}
		&.dataList{
			border-bottom:1px solid #000;
			li{
				border-top:1px solid #000;
				padding:5px 0;
				&:first-child{
					border-top:0;
				}
			}
		}
	}

	&.type02{
		ul{
			@extend .clearfix;
		}
		li{
			width:30%;
			float:left;
			figure{
				width:100%;
				.imgBox{
					text-align:center;
					overflow:hidden;
					height:148px;
					img{
						width:100%;
					}
				}
				figcaption{
					height:170px;
					overflow:hidden;
					padding:5px 5px 0 5px;
					margin-bottom:5px;
				}
			}
		}
		&.dataList{
			li{
				border:1px solid #000;
				margin-left:3%;
				&:first-child{
					margin-left:0;
				}
			}
		}
		&.list4n{
			li{
				width:23%;
				margin-left:2.6%;
			}
		}
	}

	&.type03{
		figure{
			@extend .clearfix;
			.imgBox{
				width:20%;
				float:left;

				img{
					max-width:100%;
				}
			}
			figcaption{
				width:80%;
				float:left;
				padding-right:1%;
			}
		}
	}
}

.ulStyle{
	li{
		margin-bottom:0.5em;
		ul{
			margin-left:0.5em;
			margin-top:0.5em;
			li{
				margin-bottom:0.5em;
			}
		}
	}
	&.type01{
		li{
			padding-left:10px;
			text-indent:-10px;
			&:before{
				content:"· ";
			}
			ul li:before{
				content:"- ";
			}
		}
	}
	&.type02{
		li{
			padding-left:12px;
			background:url(../images/common/dot_01.png) 0 6px no-repeat;
		}
	}
	&.type03{
		li{
			padding-left:12px;
			position:relative;
		}
		span{
			position:absolute;
			left:0;
		}
	}
}
button{
	i,span{
		vertical-align:middle;
	}
	i+span{
		margin-left:5px;
	}
}

.button {
	display: inline-block;
	zoom: 1; /* zoom and *display = ie7 hack for display:inline-block */
	*display: inline;
	vertical-align:top;
	margin: 0 2px;
	outline: none;
	cursor: pointer;
	text-align: center;
	text-decoration: none;
	font-size:14px;
	background-image:none;// Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214
	background-color:transparent;
	white-space:nowrap;
	padding: .5em 2em .55em;
	text-shadow: 0 1px 1px rgba(0,0,0,.3);
	-webkit-border-radius: .5em; 
	-moz-border-radius: .5em;
	border-radius: .5em;
	-webkit-box-shadow: 0 1px 2px rgba(0,0,0,.2);
	-moz-box-shadow: 0 1px 2px rgba(0,0,0,.2);
	box-shadow: 0 1px 2px rgba(0,0,0,.2);
	height:auto;
	line-height:1;
	&:hover{
		text-decoration: none;	
	}
	&:active {
		position: relative;
		top: 1px;
	}
	&.icon{
		text-shadow:none;
		box-shadow:none;
		padding:.2em .5em;
		border:none;
		i{
			&>span{
				@extend .hidden;
			}
		}
	}
}

.bigrounded {
	-webkit-border-radius: 2em;
	-moz-border-radius: 2em;
	border-radius: 2em;
}
.medium {
	font-size: 12px;
	padding: .4em 1.5em .42em;
}
.small {
	font-size: 11px;
	padding: .2em 1em .275em;
}

/* black */
.black {
	color: #d7d7d7;
	border: solid 1px #333;
	background: #333;
	background: -webkit-gradient(linear, left top, left bottom, from(#666), to(#000));
	background: -moz-linear-gradient(top,  #666,  #000);
	filter:  progid:DXImageTransform.Microsoft.gradient(startColorstr='#666666', endColorstr='#000000');
	&:hover {
		background: #000;
		background: -webkit-gradient(linear, left top, left bottom, from(#444), to(#000));
		background: -moz-linear-gradient(top,  #444,  #000);
		filter:  progid:DXImageTransform.Microsoft.gradient(startColorstr='#444444', endColorstr='#000000');
	}
	&:active {
		color: #666;
		background: -webkit-gradient(linear, left top, left bottom, from(#000), to(#444));
		background: -moz-linear-gradient(top,  #000,  #444);
		filter:  progid:DXImageTransform.Microsoft.gradient(startColorstr='#000000', endColorstr='#666666');
	}
}
/* gray */
.gray {
	color: #e9e9e9;
	border: solid 1px #555;
	background: #6e6e6e;
	background: -webkit-gradient(linear, left top, left bottom, from(#888), to(#575757));
	background: -moz-linear-gradient(top,  #888,  #575757);
	filter:  progid:DXImageTransform.Microsoft.gradient(startColorstr='#888888', endColorstr='#575757');
	&:hover {
		background: #616161;
		background: -webkit-gradient(linear, left top, left bottom, from(#757575), to(#4b4b4b));
		background: -moz-linear-gradient(top,  #757575,  #4b4b4b);
		filter:  progid:DXImageTransform.Microsoft.gradient(startColorstr='#757575', endColorstr='#4b4b4b');
	}
	&:active {
		color: #afafaf;
		background: -webkit-gradient(linear, left top, left bottom, from(#575757), to(#888));
		background: -moz-linear-gradient(top,  #575757,  #888);
		filter:  progid:DXImageTransform.Microsoft.gradient(startColorstr='#575757', endColorstr='#888888');
	}
}
/* white */
.white {
	color: #606060;
	border: solid 1px #b7b7b7;
	background: #fff;
	background: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#ededed));
	background: -moz-linear-gradient(top,  #fff,  #ededed);
	filter:  progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#ededed');
	&:hover {
		background: #ededed;
		background: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#dcdcdc));
		background: -moz-linear-gradient(top,  #fff,  #dcdcdc);
		filter:  progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#dcdcdc');
	}
	&:active {
		color: #999;
		background: -webkit-gradient(linear, left top, left bottom, from(#ededed), to(#fff));
		background: -moz-linear-gradient(top,  #ededed,  #fff);
		filter:  progid:DXImageTransform.Microsoft.gradient(startColorstr='#ededed', endColorstr='#ffffff');
	}
}

/*tab*/
.tab{
	li{
		position:relative;
		float:left;
		display:inline-block;
		a{
			width:100%;
			text-align:center;
			display:inline-block;
			text-decoration:none;
			color:#7b7b7b;
			&:hover,
			a:actived{
				color:#7b7b7b;
			}
		}
	}
	&.type01{
		ul{
			li{
				border:1px solid #7b7b7b;
				min-width:13.42%;
				margin-left:.2em;
				margin-bottom:.2em;
				&:first-child{
					margin-left:0; 
				}
				&.active a{
					background:$baseBgColor;
					color:#fff;
				}
				a{
					padding:.4em .6em;
				}
			}
		}	
	}
	&.type02{
		li{
			border:1px solid #7b7b7b;
			// min-width:13.42%;
			margin-right:1.5em;
			background:#f8f8f8;
			&.active{
				border: 1px solid #c2491e;
				background-color:$baseColor;
				a{
					color:#fff;
				}
			} 
			&:before{
				content:" > ";
				position:absolute;
				left:-1em;
				width:0;
				// border:0.5em solid transparent;
				// border-left-color:#7b7b7b;
				top:0.3em;
			}
			&:first-child:before{
				content:none;
			}
			a{
				padding:.3em .6em;
			}
		}
	}
}
/*tableList*/
.tableList{
	width:100%;
	border-bottom:1px solid #b8b8b8;
	table{
		width:100%;
		color:#707070;
		font-size:12px;
		th,td{
			padding:7.5px 0;
			background:#fff;
		}
		thead{
			th{
				position:relative;
				font-weight:bold;
				vertical-align:middle;
				background:#f8f8f8;
			}
		}
		tbody{
			td{	
				text-align:center;
				&.title{
					text-align:left;
					padding-left:5px;
					a{
						color:#424242;
					}
				}
			}
		}
	}
	tfoot{
		td{
			text-align:center;
		}
	}
	&.type01{
		table{
			thead{
				th{
					color:#fff;
					background:$baseBgColor;
					vertical-align:middle;
					&:before{
						content:"";
						position:absolute;
						left:0;
						width:0;
						height:50%;
						border-left:1px solid #fff;
					}
					&:first-child:before{
						content:none;
					}
				}
			}
			tbody{
				tr{
					&:nth-child(even) td{
						background:#fafafa;
					}
					td{	
						border-top:1px solid #d8d8d8;
					}
				}
			}
		}
	}
	&.type02{
		table{
			th,td{
				border-right:1px solid #b8b8b8;
				border-top:1px solid #b8b8b8;
				padding:7.5px 5px;
				&:first-child{
					border-left:1px solid #b8b8b8;
				}
			}
		}
		&.tdLeft{
			td{
				text-align:left;
			}
			tfoot td{
				text-align:left;	
			}	
		} 
		&.tdRight{
			td{
				text-align:right;
			}
			tfoot td{
				text-align:right;	
			}	
		} 
	}
}
.caution{
	font-size:13px;
	margin-bottom:10px;
	text-align:right;
	strong{
		font-weight:bold;
	}
}
.tableRegist{
	width:100%;
	font-size:12px;
	border-top:2px solid #6a6a6a;
	table{
		width:100%;
	}
	th,td{
		padding:5px;
		color:#424242;
		line-height:1.5;
		word-break:break-all;
		border-bottom:1px solid #c8c8c8;
		select,input,button,img,textarea,label,span,i{
			vertical-align:middle;
		}
		i{
			line-height:0;
		}
	}
	th{
		width:20%;
		text-align:center;
		background:#f8f8f8;
	}
	td{
		padding-left:15px;
		padding-right:15px;
		border-left:1px solid #cecece;
		width:80%;
		text-align:left;
		background-color:#fff;
		a{
			color:#424242;
		}
		&.contents{
			height:250px;
		}
		.errMsg{
			color:red !important;
		}
		#file_div{
			position:relative;
			margin-top:10px;
			&:first-child{
				margin-top:0;
			}
			.fileBtnGroup{
				float:right;
				.removeBtn{
					display:none;
					&.on{
						display:inline-block;
					}
				}
			}
		}
	}
}
.tableView{
	width:100%;
	font-size:12px;
	border-top:2px solid #6a6a6a;
	dt{
		h2{
			text-align:left;
			padding:0 15px;
			height:33px;
			line-height:33px;
			font-size:14px;
			color:#333333;
			border-bottom:1px solid #d3d3d3;
			background:#f8f8f8;
			word-break:break-all;
		}
	}
	dd{
		padding:10px 16px;
		text-align:left;
		word-break:break-all;
		border-bottom:1px solid #ebebeb;
		background-color:#fff;
		&.headSub{
			@extend .clearfix;
			border-bottom:1px solid #ebebeb;
			background-color:#fff;
			a,a:hover,a:active{
				color:#666;
			}
			.float_left{
				span{
					margin-right:10px;
				}
				a i{
					margin-right:5px;
				}
			}
			.float_right{
				ul li{
					float:left;
					margin-left:11px;
					span{
						margin-left:5px;
					}
				}
			}
		}
		&+dd{
			min-height:200px;
		}
	}
	.pageLink{
		border-top:1px solid #cdcdcd;
		.prevLink,.nexkLink{
			width:100%;
			border-bottom:1px solid #d5d5d5;
			color:#777;
			div{
				width:12%;
				display:inline-block;
				text-align:center;
				background:#f8f8f8;
				line-height:35px;
				height:35px;
				span{
					padding-left:7px;
				}
				i,span{
					vertical-align:middle;
				}
			}
			a{
				width:88%;
				padding-left:20px;
				white-space:nowrap;
				overflow:hidden;
				text-align-last:auto;
				text-overflow:ellipsis;
				color:#777;
				&:hover,&:active{
					color:#777;
				}
			}
		}
		
	}
}
.red{
	color:#f00 !important;
}
.paging{
	text-align:center;
	margin-top:20px;
	font-size:0;
	letter-spacing:0;
	word-spacing:0;
	background-color:inherit;
	overflow:hidden;
	vertical-align:top;
	
	a{
		box-sizing:border-box;
		-webkit-box-sizing:border-box;
		-moz-box-sizing:border-box;
		color:#666666;
		font-size:13px;
		margin-left:7px;
		display:inline-block;
		width:30px;
		height:30px;
		line-height:30px;
		border:1px solid #dbdbdb;
		vertical-align:middle;
		background:#fff;
		&:first-child{
			margin-left:0;
		}
		&.firstPage, &.prevPage, &.nextPage, &.lastPage{
			padding:0;
			margin:0;
		}
		&.prevPage{
			margin-left:5px;
			margin-right:20px;
		}
		&.nextPage{
			margin-right:5px;
			margin-left:20px;
		}
		&.on{
			background: #6d6d6d;
			border: 1px solid #6d6d6d;
			color: #fff;
		}
		i{
			overflow:hidden;
			span{
				text-indent:-9999px;
				display:block;
				width:0;
				height:0;  
			}
		}
	}
}
.textBox{
	div{
		display:inline-block;
		background:#fff;
		padding:10px;
		width:30%;
		margin-left:1%;
		height:200px;
		vertical-align:middle;
		line-height:1.4;
		&:first-child{
			margin-left:0;
		}
	}
	i{
		vertical-align:middle;
		margin-left:1%;
	}
}

#popup{
	position:fixed;
	display:none;
	top:0;
	left:0;
	bottom:0;
	right:0;
	text-align:center;
	&.on{
		display:block;
	}
	.dimmed{
		position:absolute;
		top:0;
		left:0;
		right:0;
		bottom:0;
		background:#000;
		opacity:0.7;
	}
	.popupContainer{
		display:inline-block;
		background:#fff;
		width:450px;
		margin-top:17%;
		position:relative;
		.popHeader{
			height:35px;
			background:#2b3b4e;
			font-weight:bold;
			color:#fff;
			line-height:35px;
			font-size:16px;
			text-align:left;
			padding-left:14px;
		}
		.popContents{
			padding:15px;
			text-align:left;
			font-size:14px;	
			line-height:1.5;
		}
		.popExit{
			position:absolute;
			right:0;
			top:0;
			color:#fff;
			padding:0 0.5em;
			i{
				line-height:35px;
			}
		}
	}
}